*{
    margin: 0;
    padding: 0;

}
.contenedor{
    width: 100%;
    height: 100%;
    margin:0;
    overflow: hidden;
  }
  
.slider{
    margin-top:15%;
    width: 100%;
    height: 600px;
    
}
.backgroundSlider {
   
    width: 100%;
    height: 600px;
    position: fixed;
    top: 0;
    left: 0;
    z-index: -1;

}
.backgroundSlider li {
    width: 100%;
    height: 600px;
    position: absolute;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    opacity: 0;
    animation: imagenes 25s linear infinite;
    -moz-animation: imagenes 25s linear infinite;
    -ms-animation: imagenes 25s linear infinite;
    -o-animation: imagenes 25s linear infinite;
    -webkit-animation: imagenes 25s linear infinite;

}
